'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { UserSubmission } from '@/lib/kv';

export default function AdminDashboard() {
  const router = useRouter();
  const [submissions, setSubmissions] = useState<UserSubmission[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState('');
  const [searchTerm, setSearchTerm] = useState('');

  useEffect(() => {
    checkAuthAndLoadSubmissions();
  }, []);

  const checkAuthAndLoadSubmissions = async () => {
    try {
      const sessionResponse = await fetch('/api/auth/session');
      if (!sessionResponse.ok) {
        router.push('/admin/login');
        return;
      }

      await loadSubmissions();
    } catch (err) {
      router.push('/admin/login');
    }
  };

  const loadSubmissions = async () => {
    try {
      setIsLoading(true);
      const response = await fetch('/api/submissions');
      if (!response.ok) {
        throw new Error('Failed to load submissions');
      }
      const data = await response.json();
      setSubmissions(data);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to load submissions');
    } finally {
      setIsLoading(false);
    }
  };

  const handleDelete = async (id: string) => {
    if (!confirm('Are you sure you want to delete this submission?')) {
      return;
    }

    try {
      const response = await fetch(`/api/submissions?id=${id}`, {
        method: 'DELETE',
      });

      if (!response.ok) {
        throw new Error('Failed to delete submission');
      }

      setSubmissions(submissions.filter(s => s.id !== id));
    } catch (err) {
      alert(err instanceof Error ? err.message : 'Failed to delete submission');
    }
  };

  const handleLogout = async () => {
    try {
      await fetch('/api/auth/logout', { method: 'POST' });
      router.push('/admin/login');
    } catch (err) {
      console.error('Logout failed:', err);
    }
  };

  const exportToCSV = () => {
    const headers = ['ID', 'Name', 'Email', 'IP Address', 'Created At', 'Model ID'];
    const rows = filteredSubmissions.map(s => [
      s.id,
      s.name,
      s.email,
      s.ipAddress,
      new Date(s.createdAt).toLocaleString(),
      s.modelId || 'N/A',
    ]);

    const csvContent = [
      headers.join(','),
      ...rows.map(row => row.map(cell => `"${cell}"`).join(',')),
    ].join('\n');

    const blob = new Blob([csvContent], { type: 'text/csv' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `submissions-${new Date().toISOString().split('T')[0]}.csv`;
    a.click();
    URL.revokeObjectURL(url);
  };

  const filteredSubmissions = submissions.filter(s =>
    s.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    s.email.toLowerCase().includes(searchTerm.toLowerCase()) ||
    s.ipAddress.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <div className="admin-dashboard">
      <header className="admin-header">
        <div className="admin-header-content">
          <div>
            <img src="/unit2go-logo.png" alt="Unit2Go" className="admin-logo" />
            <h1>Admin Dashboard</h1>
          </div>
          <button onClick={handleLogout} className="logout-button">
            Logout
          </button>
        </div>
      </header>

      <main className="admin-content">
        <div className="admin-controls">
          <div className="search-box">
            <input
              type="text"
              placeholder="Search by name, email, or IP..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </div>
          <div className="admin-actions">
            <button onClick={loadSubmissions} className="refresh-button">
              Refresh
            </button>
            <button onClick={exportToCSV} className="export-button">
              Export CSV
            </button>
          </div>
        </div>

        {error && <div className="error-message">{error}</div>}

        {isLoading ? (
          <div className="loading">Loading submissions...</div>
        ) : (
          <div className="submissions-table-container">
            <table className="submissions-table">
              <thead>
                <tr>
                  <th>Name</th>
                  <th>Email</th>
                  <th>IP Address</th>
                  <th>Created At</th>
                  <th>Model ID</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                {filteredSubmissions.length === 0 ? (
                  <tr>
                    <td colSpan={6} className="no-data">
                      {searchTerm ? 'No matching submissions found' : 'No submissions yet'}
                    </td>
                  </tr>
                ) : (
                  filteredSubmissions.map((submission) => (
                    <tr key={submission.id}>
                      <td>{submission.name}</td>
                      <td>{submission.email}</td>
                      <td>{submission.ipAddress}</td>
                      <td>{new Date(submission.createdAt).toLocaleString()}</td>
                      <td>{submission.modelId || 'N/A'}</td>
                      <td>
                        <button
                          onClick={() => handleDelete(submission.id)}
                          className="delete-button"
                        >
                          Delete
                        </button>
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
        )}

        <div className="admin-stats">
          <div className="stat-card">
            <h3>Total Submissions</h3>
            <p className="stat-number">{submissions.length}</p>
          </div>
          <div className="stat-card">
            <h3>Filtered Results</h3>
            <p className="stat-number">{filteredSubmissions.length}</p>
          </div>
        </div>
      </main>
    </div>
  );
}
